import axios from 'axios';
import SunCalc from 'suncalc';
import timestamp from 'unix-timestamp';
import React, { createContext } from 'react';
import { useHistory } from 'react-router-dom';
import LocalStorageService from '../utils/LocalStorageService';

timestamp.round = true;
export const ApiContext = createContext();

function ApiProvider(props) {
    const isUpdatedToggle = false;
    const contextHeader = {
        url: {
            auth: 'http://sancaliar82.pythonanywhere.com/auth/obtain-token',
            refresh: 'http://sancaliar82.pythonanywhere.com/auth/obtain-token/refresh',
        },
        apiKeys: ['reactor', 'pressurized', 'normal'],
        path: '/biogas/auth/',
        forecast: {
            'humidity': 0,
            'temperature': 0,
            'icon': require('../../img/weather-icon/03-berawan-AM.png')
        },
        graphData: {
            reactor: [{
                unix: timestamp.now(),
                temperature_a: 0,
                temperature_b: 0,
                pressure: 0,
                flow: 0
            }],
            pressurized: [{
                unix: timestamp.now(),
                temperature: 0,
                pressure: 0,
                level: 0,
                flow: 0
            }],
            normal: [{
                unix: timestamp.now(),
                temperature: 0,
                pressure: 0,
                flow: 0
            }]
        }
    };
    
    const apiAxios = axios.create();
    const openRequest = indexedDB.open('api_biogas');

    openRequest.onupgradeneeded = function(e) {
        contextHeader.apiKeys.forEach (
            object => e.target.result.createObjectStore(object,
                {keyPath: 'unix'}));
    };

    openRequest.onsuccess = function(e) {
        contextHeader.database = e.target.result;
        contextHeader.database.onversionchange = function(e) {
            contextHeader.database.close();
        };
    };

    const history = useHistory();
    function eject() {
        const deleteRequest = indexedDB.deleteDatabase('api_biogas');
        deleteRequest.onsuccess = function(e) {
            LocalStorageService.clear(); history.push(
                contextHeader.path
            );
        };
    };

    apiAxios.interceptors.request.use(
        config => {
            const accessToken = LocalStorageService.getAccessToken();
            if (accessToken) config.headers['Authorization'] = `Bearer ${accessToken}`;
            return config;
        },
        error => Promise.reject(error)
    );
    
    apiAxios.interceptors.response.use(
        response => {return response},
        async function (error) {
            const originalRequest = error.config;
            
            if (originalRequest.url === contextHeader.url.refresh
                && error.response.status === 401) {
                eject();
            };
    
            if (error.response.status === 401 && !originalRequest._retry) {
                originalRequest._retry = true;
    
                return apiAxios
                    .post(
                        contextHeader.url.refresh,
                        {'refresh': LocalStorageService.getRefreshToken()}
                    )
                    .then(response => {
                        if (response.status === 200) {
                            LocalStorageService.setAccessToken(response.data.access);
                            apiAxios.defaults.headers.common['Authorization'] = `Bearer ${
                                response.data.access}`;

                            return apiAxios(originalRequest);
                        } else eject();
                    })
                    .catch (error => {console.log(error);
                        eject();
                    })
            };
            return Promise.reject(error);
        });

    const contextObject = {
        logout: eject,
        getRequest:() => {
            function getDatabase(key, response) {
                let objectStore = contextHeader.database.transaction(key, 'readwrite')
                    .objectStore(key);

                objectStore.clear();
                response.data.forEach(object => objectStore
                    .add(object)
                );
                
                objectStore.getAll().onsuccess = function(e) {
                    contextHeader.graphData[key] = e.target.result
                        .slice(-60);
                };
            };

            apiAxios
                .get(`http://sancaliar82.pythonanywhere.com/biogas/updated/`)
                .then(response => contextHeader.updateStatus
                    = response.data);

            contextHeader.apiKeys.forEach (
                key => {
                    if (contextHeader.updateStatus !== undefined) {
                        if (contextHeader.updateStatus[key] !== false) {
                            apiAxios
                                .get(`http://sancaliar82.pythonanywhere.com/biogas/${key}/60`)
                                .then(response => getDatabase(
                                    key, response)
                                );
                        };
                    };
                }
            ); return !isUpdatedToggle;
        },
        _getGraphData:() => {
            const graphObject = {
                reactor: {},
                pressurized: {},
                normal: {}
            };

            contextHeader.apiKeys.forEach(
                key => {
                    let graphData = contextHeader.graphData[key];
                    let responseFields = Object.keys(
                        graphData[0]
                    );

                    responseFields.forEach(
                        sensor => {
                            graphObject[key][sensor] = graphData
                                .map(object => {
                                    let category = new Date(object.unix * 1000)
                                        .toLocaleTimeString('en-GB');
                                    return {
                                        y: object[sensor],
                                        x: category
                                    };
                                })
                        });
                }
            );
            return graphObject;
        },
        _getLastData:() => {  
            const response = [];
            contextHeader.apiKeys.forEach(
                key => response.push(contextHeader.graphData[key]
                    .slice(-1)[0]
                )
            );
            return response;
        },
        updateForecast:() => {
            axios
                .get (
                    'https://data.bmkg.go.id/datamkg/MEWS/DigitalForecast/DigitalForecast-JawaBarat.xml', 
                    {'Content-Type': 'application/xml; charset=utf-8'}
                ).then (XMLResponse => {
                    axios
                        .post (
                            'http://sancaliar82.pythonanywhere.com/biogas/weather/',
                            {'content': XMLResponse.data}
                        ).then (response => {
                            Object.keys(contextHeader.forecast).forEach(
                                key => contextHeader.forecast[key] = response.data[key]
                            );
        
                            let dateObject = new Date();
                            let sunCalcObject = SunCalc.getTimes(
                                dateObject,
                                response.data.latitude,
                                response.data.longitude
                            );
                            
                            let iconIndicator = dateObject > sunCalcObject.sunrise
                                && dateObject < sunCalcObject.sunset
                                ? 'AM':'PM';
        
                            switch(response.data.weather) {
                                case 0:
                                    contextHeader.forecast.icon = require(
                                        `../../img/weather-icon/00-cerah-${iconIndicator}.png`
                                    ); break;
                                case 1:
                                case 2:
                                    contextHeader.forecast.icon = require(
                                        `../../img/weather-icon/01-cerah-berawan-${iconIndicator}.png`
                                    ); break;
                                case 3:
                                case 4:
                                    contextHeader.forecast.icon = require(
                                        `../../img/weather-icon/03-berawan-${iconIndicator}.png`
                                    ); break;
                                case 5:
                                case 10:
                                case 45:
                                    contextHeader.forecast.icon = require(
                                        `../../img/weather-icon/05-udara-kabur-${iconIndicator}.png`
                                    ); break;
                                case 60:
                                case 61:
                                case 63:
                                    contextHeader.forecast.icon = require(
                                        `../../img/weather-icon/60-hujan-ringan-${iconIndicator}.png`
                                    ); break;
                                case 80:
                                    contextHeader.forecast.icon = require(
                                        `../../img/weather-icon/80-hujan-lokal-${iconIndicator}.png`
                                    ); break;
                                case 95:
                                case 97:
                                    contextHeader.forecast.icon = require(
                                        `../../img/weather-icon/95-hujan-petir-${iconIndicator}.png`
                                    ); break;
                                default:
                                    contextHeader.forecast.icon = require(
                                        `../../img/weather-icon/01-cerah-berawan-${iconIndicator}.png`
                                    ); break;
                            };
                        });
                });
        },
        _getForecast:() => {
            return contextHeader.forecast;
        }
    };

    return (
        <ApiContext.Provider value={contextObject}>
            {props.children}
        </ApiContext.Provider>
    );
};

export default ApiProvider;